<template>
  <v-container
    fluid
    grid-list-md>
    <v-form
      ref="form2"
      @submit.prevent="validateBeforeSubmit">
      <v-layout
        row
        wrap>
        <v-flex md12>
          <v-card class="elevation-0">
            <v-card-text>
              <v-layout
                row
                wrap>
                <v-flex md12>
                  <core-filtro-rede-empresa-loja
                    v-model="filtros"
                    :show-todas="true"
                    :obrigatorio="true"
                  />
                </v-flex>
                <v-flex md4>
                  <v-autocomplete
                    v-validate="'required'"
                    v-model="filtros.tipoLancamentoId"
                    :items="tipos"
                    :error-messages="errors.collect('Tipo')"
                    data-vv-name="Tipo"
                    label="Tipo"
                    item-value="id"
                    item-text="descricao"
                  />
                </v-flex>
                <v-flex md2>
                  <v-text-field
                    v-model="filtros.dataInicio"
                    label="Data Inicial"
                    type="date"
                  />
                </v-flex>
                <v-flex md1>
                  <span class="layout fill-height align-center justify-center">a</span>
                </v-flex>
                <v-flex md2>
                  <v-text-field
                    v-model="filtros.dataFim"
                    label="Data Final"
                    type="date"
                  />
                </v-flex>
                <v-flex md2>
                  <v-autocomplete
                    v-model="filtros.tipoRel"
                    :items="tiposRel"
                    label="Relatório em"
                    item-value="id"
                    item-text="descricao"
                  />
                </v-flex>
              </v-layout>
            </v-card-text>
          </v-card>
        </v-flex>
        <v-flex md12>
          <v-layout class="justify-end">
            <v-btn
              :loading="loadingBtn"
              type="submit"
              color="primary">Gerar</v-btn>
          </v-layout>
        </v-flex>
        <core-progress-modal :show="loading" />

        <embed
          v-if="filePDF"
          id="plugin"
          :src="filePDF"
          width="100%"
          height="1000px"
          name="plugin"
          type="application/pdf">
      </v-layout>
    </v-form>
  </v-container>
</template>

<script>
import { TiposBusiness, RelatorioBusiness } from '../../business'
import { TODAS_ID } from '../../constants'

export default {
  metaInfo: {
    titleTemplate: '%s - Relatório'
  },
  data () {
    return {
      filtros: {
        redeId: null,
        empresaId: null,
        lojaId: null,
        tipoLancamentoId: null,
        tipoRel: 'PDF',
        dataInicio: null,
        dataFim: null
      },
      filePDF: null,
      tiposRel: [],
      tipos: [],
      loadingBtn: false,
      loading: false
    }
  },
  beforeMount () {
    this.tipos.push({ id: TODAS_ID, descricao: 'Todos Lançamentos' })
    TiposBusiness.getAllTiposRelatorio()
      .then(response => {
        this.tiposRel = response.data
      })
    TiposBusiness.tiposLancamentoRepasse()
      .then(response => {
        this.tipos = this.tipos.concat(response.data)
      })
  },
  methods: {
    validateBeforeSubmit () {
      this.$validator.validate().then(result => {
        if (result && !!this.filtros.redeId && !!this.filtros.empresaId && !!this.filtros.lojaId) {
          this.loading = true
          this.loadingBtn = true
          RelatorioBusiness.geraRelatorioLanctoExtra(this.filtros)
            .then(response => {
              if (this.filtros.tipoRel === 'PDF') {
                this.filePDF = window.URL.createObjectURL(response.data)
              } else {
                const url = window.URL.createObjectURL(new Blob([response.data]))
                const link = document.createElement('a')
                link.href = url
                link.setAttribute('download', response.headers['content-disposition'].split(';')[1].split('=')[1])
                document.body.appendChild(link)
                link.click()
              }
            /*  let tipo = this.filtros.tipo === 'PDF' ? 'application/pdf' : 'application/vnd.ms-excel'
              let nameFile = this.filtros.tipo === 'PDF' ? 'relatorio_lancto_extra.pdf' : 'relatorio_lancto_extra.xls'
              // For IE using atob convert base64 encoded data to byte array
              if (window.navigator && window.navigator.msSaveOrOpenBlob) {
                var byteCharacters = atob(response.data)
                var byteNumbers = new Array(byteCharacters.length)
                for (var i = 0; i < byteCharacters.length; i++) {
                  byteNumbers[i] = byteCharacters.charCodeAt(i)
                }
                var byteArray = new Uint8Array(byteNumbers)
                var blob = new Blob([byteArray], {
                  type: tipo
                })
                window.navigator.msSaveOrOpenBlob(blob, nameFile)
              } else {
                if (this.filtros.tipo === 'PDF') {
                  this.filePDF = 'data:application/pdf;base64,' + response.data
                } else {
                  this.filePDF = 'data:application/vnd.ms-excel;base64,' + response.data
                }
              }
              */
            })
            .catch(erro => this.$root.showErro(this.$vuetify.t('$vuetify.Erro.carregarRelatorio')))
            .finally(() => {
              this.loading = false
              this.loadingBtn = false
            })
        } else {
          this.$root.showAlerta(this.$vuetify.t('$vuetify.Erro.camposObrigatorios'))
        }
      })
    }
  }
}
</script>
